header {
  position: relative;
  height: 100vh;
  min-height: 500px;
  width: 100%;
  background: #161415 url(images/header-background.jpg) no-repeat top center;
  background-size: cover !important;
  -webkit-background-size: cover !important;
  text-align: center;
  overflow: hidden;
  display: flex !important;
  align-items: center;
}

/* vertically center banner section */
header:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
}


header {
  #bgVideo {
    height: 100vh;
    transform: scale(1.5);
  }

  .banner {
    width: 100%;
    text-align: center;
    position: absolute;
    background: #25252580;
    height: 100vh;
    display: flex;
    align-items: center;
  }

  .banner-text {
    width: 100%;

    h1 {
      font: 90px/1.1em "Quicksand", sans-serif;
      color: #fff;
      letter-spacing: -2px;
      margin: 0 auto 18px auto;
    }

    .name {
      background: -webkit-linear-gradient(135deg, #00ffa4, #a668ff);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    h3 {
      font: 18px/1.9em "librebaskerville-regular", serif;
      color: #a8a8a8;
      margin: 0 auto;
